import React, {useState} from 'react';
import './contact.scss'
import shake from '../../assets/shake.svg'
import {Button, TextField} from "@material-ui/core";

const Contact = () => {
    const [message, setMessage] = useState(false);

    function handlerClick() {
        setMessage(true)
    }

    return (
        <div className={'contact'} id={'contact'}>
            <div className="left">
                <img src={shake} alt=""/>
            </div>
            <div className="right">
                <h2>Contact</h2>
                <form>
                    <input className={'input'} id="outlined-basic" variant="outlined" placeholder="Email"
                               type="text"/>
                    <textarea className={'textarea'} placeholder={'Message'} id="outlined-multiline-static">

                    </textarea>
                    <Button variant="outlined" color="secondary" onClick={() => handlerClick()}>Send</Button>
                    {message ? <span>Thanks,message send</span> : ''}
                </form>
            </div>
        </div>
    );
};

export default Contact;
