import React from 'react';
import './menu.scss'
import {ChangeColorMenuAC, ChangeUrlAc} from "../../redux/portfolioReducer";
import {useDispatch, useSelector} from "react-redux";


const Menu = (props) => {
    const dispatch = useDispatch()
    const select = useSelector(store => store.portfolio)
    const init = () => {
        props.setMenuOpen(!props.menuOpen)
        dispatch(ChangeUrlAc(undefined))
        dispatch(ChangeColorMenuAC(false))
    }
    return (
        <div className={`menu ${props.menuOpen ? 'active' : ''} `}>
            <ul>
                <li><a href="#intro" onClick={init}>Home</a></li>
                <li><a href="#portfolio" onClick={init}>Portfolio</a></li>
                <li><a href="#works" onClick={init}>Works</a></li>
                <li><a href="#testimonias" onClick={init}>Testimonias</a></li>
                <li><a href="#contact" onClick={init}>Contact</a></li>
            </ul>
        </div>
    );
};

export default Menu;
